export default{
	onLoad(opt)
	{
		this.news.ID=opt.id;
		this.getData();
		},
	data(){
		return{
			data:0,
			news: {
				ID: "",
				Author: "",
				CategoryId: "",
				CategoryName: "",
				Content: "",
				ReleaseTime: "",
				Topic: ""
				
			},
		}
	},
	methods:{
		getData()
		{
			if (!this.news.ID) return;
			uni.post("/api/cms/getArticle",{ID: this.news.ID},msg=>{
				this.news = msg.data;
			});
			
		}
	}
}
